import React, { Component } from "react";
// import { Link } from "react-router-dom";
import Input from "./Input";
import Button from "./Button";

class Main extends Component {
  render() {
    return (
      <div className="Main-page">
        <h2>Main page</h2>
        <div>
          <Input
            type="text"
            name="TextInput"
            value={this.props.value}
            changeHandler={this.props.changeHandlerText}
          />
          <Button className="Button" clickHandler={this.props.clickHandler} />
          {/* <Link id="linkBtn" to={"/Person/"+this.props.value} name={this.props.value}>{this.props.value}</Link> */}
        </div>
      </div>
    );
  }
}

export default Main;
